import { Fragment, lazy, useState, Suspense } from 'react'
import { Row, Col, ButtonGroup, Button } from 'reactstrap'
import Breadcrumbs from '@components/breadcrumbs'
import { Users, Dribbble } from 'react-feather'

const StatisticsCards = () => {
  const [selectedStat, setSelectedStat] = useState(0)

  const Content =
    selectedStat === 0
      ? lazy(() => import('./cricket/list'))
      : selectedStat === 1
      ? lazy(() => import('./football/list'))
       : selectedStat === 2
      ? lazy(() => import('./basketball/list'))
      : lazy(() => import('./basketball/list'))
    // selectedStat === 0 ? lazy(() => import('./Contests/list')) : selectedStat === 1 ? lazy(() => import('./winner/list')) : lazy(() => import('./quiz/list'))
    // selectedStat === 0 ? lazy(() => import('./cricket/list')) : selectedStat === 1 ? lazy(() => import('./football/list')) : selectedStat === 2 ? lazy(() => import('./basketball/list')) : lazy(() => import('./playerreport'))
     
  return (
    <Fragment>
    <Breadcrumbs breadCrumbTitle='Jersey' breadCrumbActive='List' />
      <Row>
        <Col md={12} sm={12}>
          <ButtonGroup className="mb-1">
            <Button
              outline
              color="primary"
              onClick={() => setSelectedStat(0)}
              active={selectedStat === 0}
            >
              <Users size={14} />
              <span className="align-middle ml-25">Cricket</span>
            </Button>
            <Button
              outline
              color="primary"
              onClick={() => setSelectedStat(1)}
              active={selectedStat === 1}
            >
              <Dribbble size={14} />
              <span className="align-middle ml-25">Football</span>
            </Button>
            {/* <Button
              outline
              color="primary"
              onClick={() => setSelectedStat(2)}
              active={selectedStat === 2}
            >
              <Dribbble size={14} />
              <span className="align-middle ml-25">Basketball</span>
            </Button> */}
          </ButtonGroup>
        </Col>
      </Row>
      <Suspense>
        <Content />
      </Suspense>
    </Fragment>
  )
}

export default StatisticsCards
